.form {
  display: -ms-flexbox;
  display: flex; }
  .form .form__field {
    padding: 10px;
    width: 50%; }
  .form .form__input {
    border-radius: 5px;
    font-size: 20px;
    width: 100%;
    padding: 15px;
    padding-left: 25px;
    background-color: transparent;
    border: 1px solid grey;
    background: url(../../../../images/dollar-icon.png) no-repeat 5px center;
    background-size: 20px; }
  .form .form__btn {
    font-size: 20px;
    border: 0;
    width: 100%;
    padding: 15px;
    background-color: green;
    text-transform: capitalize;
    font-weight: 600;
    border: 1px solid transparent;
    border-radius: 5px; }



